import React, { Component } from "react"
import IssuesList from "@components/Dashboard/Inventory/Issues/IssuesList"
import { Button, Row, Col } from "react-bootstrap"
import { FaChevronLeft, FaChevronRight } from "react-icons/fa"

class FilteredIssues extends Component {
  state = {
    currentPage: 1,
  }

  pageSwitcher = value => {
    if (value === "next") {
      this.setState({ currentPage: this.state.currentPage + 1 })
    } else {
      this.setState({ currentPage: this.state.currentPage - 1 })
    }
  }

  render() {
    const { inventoryId, issues } = this.props
    const { currentPage } = this.state
    const lastItem = currentPage * 10
    const firstItem = lastItem - 10

    let formatedIssues = []
    formatedIssues =
      issues.length && issues.sort((a, b) => new Date(b.createdAt) - new Date(a.createdAt)).slice(firstItem, lastItem)
    const renderPageNumbers = () => {
      return (
        <Row className="pagination issues">
          <Col>
            <div className="arrows d-flex justify-content-end">
              <Button onClick={() => this.pageSwitcher("prev")} disabled={firstItem === 0}>
                <FaChevronLeft />
              </Button>
              <Button onClick={() => this.pageSwitcher("next")} disabled={lastItem >= issues.length}>
                <FaChevronRight />
              </Button>
            </div>
          </Col>
        </Row>
      )
    }

    return (
      <>
        {formatedIssues.length ? (
          <>
            <ul className="ms-scrollable ps ps--active-y">
              {formatedIssues.map(issue => (
                <IssuesList issue={issue} {...this.props} />
              ))}
            </ul>
            {renderPageNumbers()}
          </>
        ) : (
          <div className="media ms-email clearfix">
            <div className="media-body ms-email-details">
              <p className="ms-email-msg">No issues...</p>
            </div>
          </div>
        )}
      </>
    )
  }
}

export default FilteredIssues
